.list-overflow-container {
  height: 1000px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
  width: 1500px;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

ul.search-results img {
  margin: 0 auto;
}
.card {
  background: #fff;
  border-radius: 2px;
  height: 400px;
  margin: 1rem;
  position: relative;
  width: 270px;
  box-shadow: 0 3px 6px #6f6666, 0 3px 6px #6f6666;
  text-align: left;
}

.card > .img-container {
  height: 70%;
  overflow: hidden;
  text-align: center;
  background:#343a40;
}
.card > .img-container > img {
  width: 70%;
  padding-top: 1rem;
}

.card > .content {
  padding-top: -4rem;
  padding-left: -2rem;
  padding-right: -2rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 60px;
  padding-top: 140px;
  margin-top: 0;
}

form.search {
  overflow: auto;
  margin-bottom: 10px;
}

form.search button[type="submit"] {
  float: right;
  margin-top: 10px;
}
ul.search-results img {
  margin: 0 auto;
}
ul.search-results img {
  margin: 0 auto;
}
nav.navbar.navbar-default {
  margin-bottom: 0;
}
.footer {
  min-height: 100px;
  background: rgb(95, 95, 95);
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: rgb(56, 55, 55);
  color: whitesmoke;
  font-weight: bold;
}

.wrapper {
  min-height: 100%;
  padding-bottom: 100px;
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

* {
  box-sizing: border-box;
}

html,
body,
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}

